import React, {useEffect, useState} from "react"
import Nav from "../components/Nav"
import Footer from "../components/Footer";
// import "bootstrap/dist/js/bootstrap.bundle"
import "../styles/customTheme.styles.scss";
import "../style.css"
import CustomNavbar from "../components/custom-navbar/custom-navbar";

const navColor = "#5a5a5a";

export default function ContactUs() {
    useEffect(() => {
        setLatitude("35.624595");
        setLongitude("51.419667");
    }, [])
    const oathBackground = "#ffffff";
    const [latitude, setLatitude] = useState("0")
    const [longitude, setLongitude] = useState("0")
    const createMarkup = () => {
        return {__html: `<iframe src="https://maps.google.com/maps?q=${latitude},${longitude}&hl=es;z=14&amp;output=embed" />`};
    }
    return (
        <React.Fragment>
            <div className="container">
                <CustomNavbar nameClass={navColor}/>
            </div>
            <h1>SALAM</h1>
            <div dangerouslySetInnerHTML={createMarkup()}/>
        </React.Fragment>
    );
}
