import React from "react";

const Wait = ({pageError}) => {
    return(
        <React.Fragment>
                <div className="d-flex justify-content-center">
                    <div className="spinner-border m-5" role="status">
                        <span className="visually-hidden">Loading...</span>
                    </div>
                </div>
                <div className="d-flex justify-content-center">
                    <h1>{pageError}</h1>
                </div>
            </React.Fragment>
    )
}
export default Wait;